import { Component, Prop, h, State, Listen } from '@stencil/core';

export type SidePanelDirection = 'left' | 'right';
export type SidePanelSize = 'xs' | 'sm' | 'md' | 'lg' | 'xl';

const panelVariants = {
    base: 'z-nav !fixed h-full min-w-panel-xs bg-white transition-transform shadow-md',
    origin: {
        left: 'left-0 top-0',
        right: 'right-0 top-0',
    },
    isExpanded:
        'border-l-med border-l-2 transition-transform duration-[300ms] ease-[cubic-bezier(0.455,0.03,0.515,0.955)]',
    fixed: {
        true: 'fixed h-full top-0',
        false: 'absolute h-full',
    },
    noScrollbar: 'overflow-y-hidden',
};

const sizeVariants = {
    base: 'overflow-x-hidden overflow-y-auto h-full flex flex-col',
    size: {
        xs: 'w-panel-xs',
        sm: 'w-panel-sm',
        md: 'w-panel-md',
        lg: 'w-panel-lg',
        xl: 'w-panel-xl',
    },
};

/**
 * @category Overlays
 * @description A sliding side panel component for displaying secondary content or forms. Features customizable positioning, backdrop, and animation options.
 *
 * @slot content - Used for placing the content of the side panel.
 */
@Component({
    tag: 'atui-side-panel',
    shadow: false,
})
export class AtuiDialogComponent {
    /**
     * Size of the size panel
     */
    @Prop()
    size: SidePanelSize = 'xs';

    /**
     * ID of the panel
     */
    @Prop()
    panel_id: string;

    /**
     * Title displayed in the side panel
     */
    @Prop()
    panel_title: string;

    /**
     * Subtitle displayed in the side panel
     */
    @Prop()
    panel_subtitle: string;

    /**
     *  Position of the side panel
     */
    @Prop()
    origin: SidePanelDirection = 'right';

    /**
     * Puts a scrollbar on the sidepanel if set
     */
    @Prop()
    has_scrollbar: boolean = true;

    /**
     * Displays a close button if set
     */
    @Prop()
    has_close_button: boolean = true;

    /**
     * Will close the sidepanel if clicked off when set
     */
    @Prop()
    close_backdrop: boolean = false;

    /**
     * If sidepanel should used fixed positioning (otherwise absolute)
     */
    @Prop()
    fixed = true;

    @State()
    isExpanded = false;

    @State()
    sidePanelWrapper: HTMLElement;

    @State()
    panelDialog: HTMLDialogElement;

    handleClose(): void {
        this.panelDialog.close();
        this.isExpanded = false;
    }

    get panelClasses(): string {
        return `${panelVariants.base} ${panelVariants.origin[this.origin]} ${this.isExpanded ? panelVariants.isExpanded : ''} 
        ${this.fixed ? panelVariants.fixed.true : panelVariants.fixed.false} ${!this.has_scrollbar ? panelVariants.noScrollbar : ''}`;
    }

    get sizeClasses(): string {
        return `${sizeVariants.base} ${sizeVariants.size[this.size]}`;
    }

    @Listen('mousedown', { target: 'document' })
    offClickHandler(event: any) {
        if (!this.close_backdrop || !this.panelDialog.open) return;
        if (!this.sidePanelWrapper.contains(event.target)) {
            this.handleClose();
        }
    }

    render() {
        return (
            <div>
                <dialog
                    id={this.panel_id}
                    ref={(el) => (this.panelDialog = el)}
                >
                    <div
                        class={`${this.panelClasses} ${this.sizeClasses}`}
                        ref={(el) => (this.sidePanelWrapper = el)}
                        data-name="panel-wrapper"
                    >
                        <div class={'sticky top-0 z-nav'}>
                            <atui-header
                                header_title={this.panel_title}
                                subtitle={this.panel_subtitle}
                            >
                                {this.has_close_button && (
                                    <span
                                        class={'rounded-full hover:bg-gray-100'}
                                        slot={'actions'}
                                    >
                                        <i
                                            class="material-icons md-16 right-16 top-20 cursor-pointer p-8 !text-[18px]"
                                            onClick={() => this.handleClose()}
                                            data-name="panel-close"
                                        >
                                            close
                                        </i>
                                    </span>
                                )}
                            </atui-header>
                        </div>

                        <div class={'flex w-full flex-1 flex-col'}>
                            <slot name={'content'} />
                        </div>
                    </div>
                </dialog>
            </div>
        );
    }
}
